import React, { useState, useEffect } from "react";
import {
  signInWithEmailAndPassword,
  onAuthStateChanged,
  createUserWithEmailAndPassword,
} from "firebase/auth";
import { auth } from "../../firebase.js";

import { useNavigate } from "react-router-dom";
import { grey } from "@mui/material/colors";
import { TextField, Checkbox, Button, styled } from "@mui/material";
import styles from "./Welcome.module.css";
import validator from "validator";

const Welcome = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isRegistering, setIsRegistering] = useState(false);
  const [registerInformation, setRegisterInformation] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    name: "",
    surname: "",
    phone: "",
  });
  const [errorEmail, setErrorEmail] = useState(false);
  const [msgErrorEmail, setmsgErrorEmail] = useState("");
  const [pwdError, setPwdError] = useState(false);
  const [msgErrorPwd, setMsgErrorPwd] = useState("");
  const [phoneError, setPhoneError] = useState(false);
  const [msgErrorPhone, setmsgErrorPhone] = useState("");

  const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" };

  const label = { inputProps: { "aria-label": "Checkbox demo" } };

  const ColorButton = styled(Button)(({ theme }) => ({
    color: theme.palette.getContrastText(grey[700]),
    margin: "15px 15px 15px 0",
    // width: "250px",
    backgroundColor: grey[900],
    "&:hover": {
      backgroundColor: grey[700],
    },
  }));

  const BackButton = styled(Button)(({ theme }) => ({
    color: theme.palette.getContrastText(grey[700]),
    color: grey[500],
    "&:hover": {
      backgroundColor: grey[500],
      color: grey[50],
    },
  }));

  const navigate = useNavigate();

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user) {
        navigate("/homepage");
      }
    });
  }, []);

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleSignIn = () => {
    signInWithEmailAndPassword(auth, email, password)
      .then(() => {
        navigate("/homepage");
      })
      .catch((err) => alert(err.message));
  };

  const handleRegister = () => {
    const EMAIL_REGEXP =
      /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu;

    if (registerInformation.email === "") {
      setErrorEmail(true);
      setmsgErrorEmail("E-mail    ");
      return;
    } else if (!EMAIL_REGEXP.test(registerInformation.email)) {
      setErrorEmail(true);
      setmsgErrorEmail("  E-mail");
      return;
    } else if (registerInformation.password === "") {
      setPwdError(true);
      setMsgErrorPwd("    ");
      return;
    } else if (registerInformation.password.length < 6) {
      setPwdError(true);
      setMsgErrorPwd("    6 ");
      // alert("Please confirm that password are the same");
      return;
    } else if (!validator.isMobilePhone(registerInformation.phone, ["ru-RU"])) {
      setPhoneError(true);
      setmsgErrorPhone("   ");
      // alert("Please confirm that password are the same");
      return;
    } else if (
      registerInformation.password !== registerInformation.confirmPassword
    ) {
      setPwdError(true);
      setMsgErrorPwd("  ");
      // alert("Please confirm that password are the same");
      return;
    }
    createUserWithEmailAndPassword(
      auth,
      registerInformation.email,
      registerInformation.password,
      registerInformation.name,
      registerInformation.surname,
      registerInformation.phone
    )
      .then(() => {
        navigate("/homepage");
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className={styles.welcome}>
      <div className={styles.loginRegisterContainer}>
        {isRegistering ? (
          <>
            <div className={styles.item}>
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="email"
                placeholder="E-MAIL"
                error={errorEmail}
                helperText={msgErrorEmail}
                value={registerInformation.email}
                onChange={(e) => {
                  setErrorEmail(false);
                  setmsgErrorEmail("");
                  setRegisterInformation({
                    ...registerInformation,
                    email: e.target.value,
                  });
                }}
              />
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="password"
                placeholder=""
                error={pwdError}
                helperText={msgErrorPwd}
                value={registerInformation.password}
                onChange={(e) => {
                  setPwdError(false);
                  setMsgErrorPwd("");
                  setRegisterInformation({
                    ...registerInformation,
                    password: e.target.value,
                  });
                }}
              />
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="name"
                placeholder=""
                // error={pwdError}
                // helperText={msgErrorPwd}
                value={registerInformation.name}
                onChange={(e) => {
                  setRegisterInformation({
                    ...registerInformation,
                    name: e.target.value,
                  });
                }}
              />
            </div>
            <div className={styles.item}>
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="tel"
                placeholder=" "
                error={phoneError}
                helperText={msgErrorPhone}
                value={registerInformation.phone}
                onChange={(e) => {
                  setPhoneError(false);
                  setmsgErrorPhone("");
                  setRegisterInformation({
                    ...registerInformation,
                    phone: e.target.value,
                  });
                }}
              />
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="password"
                placeholder=" "
                value={registerInformation.confirmPassword}
                onChange={(e) => {
                  setPwdError(false);
                  setMsgErrorPwd("");
                  setRegisterInformation({
                    ...registerInformation,
                    confirmPassword: e.target.value,
                  });
                }}
              />
              <TextField
                inputProps={{ style: inputStyle }}
                margin="normal"
                fullWidth
                variant="standard"
                type="surname"
                placeholder=""
                // error={pwdError}
                // helperText={msgErrorPwd}
                value={registerInformation.surname}
                onChange={(e) => {
                  setRegisterInformation({
                    ...registerInformation,
                    surname: e.target.value,
                  });
                }}
              />
            </div>

            <div className={styles.item}>
              <Checkbox {...label} />
              <Checkbox {...label} />
              <ColorButton variant="contained" onClick={handleRegister}>
                
              </ColorButton>
              <BackButton
                variant="text"
                className="create-account-button"
                onClick={() => setIsRegistering(false)}
              >
                
              </BackButton>
            </div>
          </>
        ) : (
          <>
            <div className={styles.header}>
              <div className={styles.headerline}>
                <span className={styles.headerS}></span>
                <TextField
                  margin="normal"
                  fullWidth
                  variant="standard"
                  inputProps={{ style: inputStyle }}
                  type="email"
                  placeholder="E-MAIL"
                  onChange={handleEmailChange}
                  value={email}
                />
                <TextField
                  inputProps={{ style: inputStyle }}
                  margin="normal"
                  fullWidth
                  variant="standard"
                  type="password"
                  onChange={handlePasswordChange}
                  value={password}
                  placeholder=""
                />
                <span
                  onClick={() => navigate("/reset")}
                  className={styles.text}
                >
                  {"\n"}
                   ?
                  {"\n"}
                </span>
                <ColorButton variant="contained" onClick={handleSignIn}>
                  
                </ColorButton>
              </div>
              <div className={styles.headerline}>
                <span className={styles.headerS}></span>
                <span className={styles.headerText}>
                     ,   ? 
                     
                  {"\n"}
                  <br />
                        
                      
                </span>

                <ColorButton
                  color="primary"
                  onClick={() => setIsRegistering(true)}
                  variant="text"
                >
                  
                </ColorButton>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export { Welcome };
